import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';

// material-ui
import { useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import PhotoFolder from './component/PhotoFolder';
import Pagination from './component/Pagination';
import PhotoGallery from './component/PhotoGallery';
import PhotoTable from './component/PhotoTable';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //

const App = ({ onPhotoSelected }) => {
    console.log('photo app running');
    const localization = useSelector((state) => state.localization); //read only (language related)
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    const globalization = useSelector((state) => state.globalization);
    const [loginData] = useState(globalization.baseData.loginData);
    let navigate = useNavigate(); //react-router-dom

    //states [所有文件夹，当前文件夹，待上传文件，文件夹下所有图片，当前页，页面大小]
    let [folderArr, setFolderArr] = useState([]);
    let [currentFolder, setCurrentFolder] = useState(langPack.folder.currentFolder);
    let [photoArr, setPhotoArr] = useState([]);
    let [page, setPage] = useState(0);
    let [page_size, setPage_size] = useState(10);
    let [displayMode, setDisplayMode] = useState('table');
    let [tableColumns, setTableColumns] = useState([]);
    let uploadTimer = 0; //节流
    const theme = useTheme();
    const photoCols = useMediaQuery(theme.breakpoints.down('md')) ? 3 : 10;

    useEffect(() => {
        console.log('display mode changed: ', displayMode);
        return;
    }, [displayMode]);
    useEffect(() => {
        setLangPack(Languages[localization.locale]);
        return;
    }, [localization.locale]);
    useEffect(() => {
        console.log('current folder:', currentFolder);
        let group_id = currentFolder !== langPack.folder.currentFolder ? currentFolder : -1;
        getPhotos({ group_id, current_page: page + 1, page_size });
        return;
    }, [currentFolder]);

    useEffect(() => {
        console.log('I want pics');
        if (!loginData) return navigate('/');
        getFolders();
        tableFormatter();
        return () => {
            console.log('I leave pics');
        };
    }, []);

    //____________一些函数_______________________________________________

    let folderRefreshTimer;
    async function manageFolder(operation, groupObj) {
        let paramObj = ma.photoFolderManageQuery({ operation, groupObj });
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, msg ? msg.response.result : '');
        let res = ma.photoFolderManageCheck(msg);
        let { status, result } = res.response;
        queryResult(status, 'ok');
        if (status) setTimeout(getFolders, 1234);
        return result;
    }
    async function getFolders(obj) {
        let paramObj = ma.photoFolderQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, msg ? msg.response.result : '');
        let res = ma.photoFolderCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        clearTimeout(uploadTimer);
        uploadTimer = setTimeout(() => {
            let newArr = result.map((item) => {
                return { ...item, id: String(item.id) };
            });
            setFolderArr(newArr);
        }, 1234);
        return result;
    }
    async function getPhotos(obj) {
        if (!obj) obj = {};
        let paramObj = ma.photoQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, msg ? msg.response.result : '');
        let res = ma.photoCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        setPhotoArr(result);
        return result;
    }
    async function uploadPhotos(photoFile) {
        let paramObj = ma.photoUploadQuery({ file_name: photoFile.name, image_bytes: photoFile, group_id: currentFolder });
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, msg ? msg.response.result : '');
        let res = ma.photoUploadCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        queryResult(status, langPack.action.uploadSuccess);
        if (folderRefreshTimer !== undefined) clearTimeout(folderRefreshTimer);
        folderRefreshTimer = setTimeout(() => {
            getPhotos({ group_id: currentFolder });
        }, 2000);
        return result;
    }

    const handleChangePage = (newPage) => {
        setPage(newPage);
        let obj = { current_page: newPage + 1, page_size };
        if (currentFolder && currentFolder !== langPack.folder.currentFolder) obj.group_id = currentFolder;
        getPhotos(obj);
    }; //翻页
    const handleChangeRowsPerPage = (newSize) => {
        setPage_size(newSize);
        let obj = { current_page: page + 1, page_size: newSize };
        if (currentFolder && currentFolder !== langPack.folder.currentFolder) obj.group_id = currentFolder;
        getPhotos(obj);
    }; //页面大小调整
    const onRowClick = (obj, e) => {
        // let { id, row } = obj;
    }; //单元格点击

    let onTableSelection = (selectedArr) => {
        if (onPhotoSelected) onPhotoSelected(selectedArr);
        console.log(selectedArr.length + ' selected');
        return;
    }; //选择图片

    //____________ Table functions ___________________________________________________
    function tableFormatter(obj) {
        const columns = [
            {
                field: 'imgWithURL',
                headerName: langPack.table.tableHeader,
                flex: 1,
                minWidth: 50,
                valueGetter: ({ row }) => row,
                valueFormatter: ({ value }) => value.url,
                renderCell: ({ value }) => (
                    <a href={value.url} target="_blank">
                        <img {...srcset(value.url, 100, 100, 1, 1)} style={{ width: '93%' }} alt="mainPic" />
                    </a>
                )
            },
            {
                field: 'file_name',
                flex: 1,
                headerName: langPack.table.file_name
            },
            {
                field: 'reference_count',
                flex: 1,
                headerName: langPack.table.reference_count
            },
            {
                field: 'gmt_modified',
                flex: 1,
                headerName: langPack.table.gmt_modified
            }
        ];
        if (obj) {
            //let { score } = obj;
        }
        setTableColumns(columns);
    }
    //____________JSX函数_______________________________________________

    return (
        <MainCard title="Photos --图库">
            <PhotoFolder
                folderArr={folderArr}
                currentFolder={currentFolder}
                setCurrentFolder={setCurrentFolder}
                manageFolder={manageFolder}
                uploadPhotos={uploadPhotos}
                setPage={setPage}
                displayMode={displayMode}
                setDisplayMode={setDisplayMode}
                langPack={langPack}
            />
            {displayMode === 'table' ? (
                <PhotoTable
                    tableRows={photoArr}
                    tableColumns={tableColumns}
                    page={page}
                    page_size={page_size}
                    localization={localization}
                    handleChangePage={handleChangePage}
                    handleChangeRowsPerPage={handleChangeRowsPerPage}
                    onRowClick={onRowClick}
                    onTableSelection={onTableSelection}
                    langPack={langPack}
                />
            ) : (
                <>
                    <Pagination
                        page={page}
                        page_size={page_size}
                        handleChangePage={handleChangePage}
                        handleChangeRowsPerPage={handleChangeRowsPerPage}
                        langPack={langPack}
                    />
                    <PhotoGallery
                        photoArr={photoArr}
                        rowHeight={150}
                        photoCols={photoCols}
                        photoSizing={{ width: 100, height: 100 }}
                        currentFolder={currentFolder}
                        langPack={langPack}
                    />
                </>
            )}
        </MainCard>
    );
};

App.propTypes = {
    onPhotoSelected: PropTypes.func
};

export default App;
