import PropTypes from 'prop-types';

// material-ui
import { TablePagination } from '@mui/material';

function Pagination({ page, page_size, handleChangePage, handleChangeRowsPerPage }) {
    return (
        <TablePagination
            component="div"
            labelDisplayedRows={({ from, to }) => {
                return `${from}–${to}`;
            }}
            sx={{
                '& .MuiTablePagination-toolbar': { height: '29px', padding: '1px' }
            }}
            page={page}
            count={-1}
            onPageChange={(event, newPage) => handleChangePage(newPage)}
            rowsPerPage={page_size}
            onRowsPerPageChange={(event) => handleChangeRowsPerPage(parseInt(event.target.value))}
        />
    );
}

Pagination.propTypes = {
    page: PropTypes.number,
    page_size: PropTypes.number,
    handleChangePage: PropTypes.func,
    handleChangeRowsPerPage: PropTypes.func
};

export default Pagination;
