import { useState } from 'react';
import PropTypes from 'prop-types';
import Photo from 'views/photo';

// material-ui
import { Button, Dialog, DialogTitle } from '@mui/material';

function PhotoSelector({ langPack, dialogOpen, setDialogOpen, onPhotoConfirm }) {
    //cloud-Photo 相关
    let [cloudImageArr, setCloudImageArr] = useState([]); //导入的云图片

    //Cloud-Photo组件 相关事件
    let onPhotoSelected = (value) => {
        if (!value) return;
        setCloudImageArr(value);
    }; //选择了图片，则存到当前component里

    let onDialogClose = (submit) => {
        setDialogOpen(false);
        if (submit) return onPhotoConfirm(cloudImageArr);
        return setCloudImageArr([]); //清除传入值
    }; //对话框关闭

    return (
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
            <Dialog onClose={() => onDialogClose(true)} open={dialogOpen}>
                <DialogTitle>{langPack.steps.selectFromCloud}</DialogTitle>
                <Photo onPhotoSelected={onPhotoSelected} />
                <Button variant="contained" color="primary" onClick={() => onDialogClose(true)}>
                    {langPack.common.yes}
                </Button>
                <Button variant="contained" color="error" onClick={() => onDialogClose()}>
                    {langPack.common.no}
                </Button>
            </Dialog>
        </div>
    );
}

PhotoSelector.propTypes = {
    dialogOpen: PropTypes.bool,
    setDialogOpen: PropTypes.func,
    onPhotoConfirm: PropTypes.func,
    langPack: PropTypes.object
};

export default PhotoSelector;
